<template>
  <!-- Toolbar container -->
  <div class="toolbar">
    <!-- Tools section -->
    <div class="tools">
      <!-- Patient List link -->
      <router-link to="/home" style="text-decoration: none" class="normal">
        <!-- Patient List tool -->
        <div class="tool">
          <!-- Reduce the icon size based on screenwidth -->
          <i
            v-if="$matches.mediumLaptop.min"
            class="fa-solid fa-people-arrows fa-xl"
          ></i>
          <i v-else class="fa-solid fa-people-arrows fa-lg"></i>
          <h4 class="section-tag">Patient List</h4>
        </div>
      </router-link>

      <!-- Quality Check tool -->
      <div
        class="tool"
        :class="{ active: selectedTool === 'QualityCheck' }"
        @click="selectTool('QualityCheck')"
        @keydown.prevent="handleKeyDown"
      >
        <!-- Reduce the icon size based on screenwidth -->
        <i
          v-if="$matches.mediumLaptop.min"
          class="fa-solid fa-file-circle-check fa-xl"
        ></i>
        <i v-else class="fa-solid fa-file-circle-check fa-lg"></i>
        <h4 class="section-tag">Quality Check</h4>
      </div>

      <!-- Graphical Analysis tool -->
      <div
        class="tool"
        :class="{ active: selectedTool === 'GraphicalAnalysis' }"
        @click="selectTool('GraphicalAnalysis')"
        @keydown.prevent="handleKeyDown"
      >
        <!-- Reduce the icon size based on screenwidth -->
        <i
          v-if="$matches.mediumLaptop.min"
          class="fa-sharp fa-solid fa-microscope fa-xl"
        ></i>
        <i v-else class="fa-sharp fa-solid fa-microscope fa-lg"></i>
        <h4 class="section-tag">Analysis</h4>
      </div>

      <!-- Report Maker tool -->
      <div
        class="tool"
        :class="{ active: selectedTool === 'ReportMaker' }"
        @click="selectTool('ReportMaker')"
        @keydown.prevent="handleKeyDown"
      >
        <!-- Reduce the icon size based on screenwidth -->
        <i
          v-if="$matches.mediumLaptop.min"
          class="fa-regular fa-file-pdf fa-xl"
        ></i>
        <i v-else class="fa-regular fa-file-pdf fa-lg"></i>
        <h4 class="section-tag">Report</h4>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // Initially selected tool
      selectedTool: 'QualityCheck', // Set the initial value based on the previous page
    };
  },
  methods: {
    // Method to select a tool and emit an event with the selected tool so that parent can handle.
    selectTool(tool) {
      if (this.selectedTool === tool) {
        return; // If the tool is already selected, do nothing
      }
      this.selectedTool = tool;
      // Emit the 'tool-selected' event with the selected tool as the payload
      this.$emit('tool-selected', tool);
    },
    handleKeyDown(event) {
      console.log(event);
      if (event.key === '1') {
        this.selectTool('QualityCheck');
      } else if (event.key === '2') {
        this.selectTool('GraphicalAnalysis');
      } else {
        this.selectTool('ReportMaker');
      }
    },
  },
  mounted() {
    // Set the selected tool based on the last viewed tool saved in local storage.
    this.selectedTool = window.localStorage.getItem('currentView');
  },
};
</script>

