<!-- 
    HEADER COMPONENT THAT IS RENDERED INSIDE OF ANALYSIS.VUE
    SHOWS BASIC USER INFORMATION (LEFT) AND TOOLBAR (RIGHT) OR BUTTONS (QUALITY CHECK).
    COMPONENT PART OF  AREA 19, OCG WEB APP.
    DEVELOPPED IN MONTREAL, QC, CANADA.
    JULY 2023.
  -->
<template>
  <!-- Header container -->
  <div class="header-container">
    <!-- Patient information section -->
    <div class="patient-info-container">
      <!-- Patient name section -->
      <div class="patient-name">
        <!-- Label for patient name -->
        <h3 class="label">PATIENT</h3>
        <!-- Display patient first and last name -->
        <h3>{{ patientName }} {{ patientLastName }}</h3>
      </div>

      <!-- Breathing rate section -->
      <div class="breathing-rate">
        <!-- Display patient's resting heart rate -->
        <h1>{{ patientBrate }}</h1>
        <!-- Label for resting heart rate -->
        <h3 class="label" style="text-align: left">RESTING <br />HEART RATE</h3>
      </div>

      <!-- Patient information details section -->
      <div class="info">
        <!-- Age -->
        <div>
          <!-- Label for age -->
          <h3 class="label">AGE</h3>
          <!-- Display patient's age -->
          <h3>{{ patientage }}</h3>
        </div>
        <!-- Weight -->
        <div>
          <!-- Label for weight -->
          <h3 class="label">WEIGHT</h3>
          <!-- Display patient's weight in kilograms -->
          <h3>{{ patientweight }} Kg</h3>
        </div>
        <!-- Height -->
        <div>
          <!-- Label for height -->
          <h3 class="label">HEIGHT</h3>
          <!-- Display patient's height in centimeters -->
          <h3>{{ patientheight }} cm</h3>
        </div>
        <!-- Gender -->
        <div>
          <!-- Label for gender -->
          <h3 class="label">GENDER</h3>
          <!-- Display patient's gender -->
          <h3>{{ patientgender }}</h3>
        </div>
      </div>
    </div>

    <!-- Tools section for GraphicalAnalysis -->
    <div v-if="activeComponent == 'GraphicalAnalysis'" class="tools">
      <!-- Display ToolStrip component -->
      <ToolStrip />
    </div>

    <!-- QualityCheck section -->
    <div v-if="activeComponent == 'QualityCheck'" class="quality-screen-toggle">
      <!-- Check Breathing Splits option -->
      <div
        class="toggleQaContainer"
        :class="{ clicked: checkContours == false }"
        @click="handleclick('CheckBreathingSplits')"
        @keydown.space.prevent="handleKeyDown"
      >
        <IconNumber />
        <h2>CHECK BREATHING SPLITS</h2>
      </div>

      <div
        class="toggleQaContainer"
        :class="{ clicked: checkContours }"
        @click="handleclick('CheckContours')"
        @keydown.space.prevent="handleKeyDown"
      >
        <!-- Check Contours option -->
        <IconNumber :number="2" />
        <h2>CHECK CONTOURS</h2>
      </div>
    </div>
  </div>
</template>

<script>
// Import the Vuex store
// import store from '/src/store/index.js';

// Import the ToolStrip component
import ToolStrip from './ToolStrip.vue';
import IconNumber from './IconNumber.vue';

export default {
  // Register the ToolStrip component
  components: { ToolStrip, IconNumber },

  // Define props for the component
  props: {
    // Active component indicates the currently displayed component
    activeComponent: { type: String, default: 'analysis' },
  },

  // Component data properties
  data() {
    return {
      // Patient information data
      patient: null,
      patientName: null,
      patientLastName: null,
      patientBrate: null,
      patientage: null,
      patientweight: null,
      patientheight: null,
      patientgender: null,
      // State to track if checkContours is active
      checkContours: null,
    };
  },

  // Lifecycle hook: mounted

  mounted() {
    const currentQaView = window.sessionStorage.getItem('currentQaView');
    if (currentQaView) {
      this.handleclick(currentQaView);
    } else {
      // Initialize checkContours to false
      this.checkContours = false;
    }

    // Get the current patient data from local storage
    this.patient = JSON.parse(window.localStorage.getItem('currentPatient'));

    // Extract and format patient information
    this.patientName = this.patient.name.toUpperCase();
    this.patientLastName = this.patient.lastname.toUpperCase();
    this.patientBrate = this.patient.breathingrate;
    this.patientage = this.patient.age;
    this.patientweight = this.patient.weight;
    this.patientheight = this.patient.height;
    this.patientgender = this.patient.gender;
  },

  // Component methods
  methods: {
    // Handle click events for quality check options
    handleclick(text) {
      if (text === 'CheckBreathingSplits') {
        this.checkContours = false;
      } else {
        this.checkContours = true;
      }

      window.sessionStorage.setItem('currentQaView', text);

      // Emit the 'updatequalitycheck' event with the checkContours value
      this.$emit('updatequalitycheck', this.checkContours);
    },
  },
};
</script>

