<template>
  <SkeletonLoaderCBS v-show="loading" :allowedWidth="allowedWidth" />
  <Transition name="slide-fade">
    <div
      v-show="!loading"
      class="slice"
      :style="{ width: allowedWidth + 'px' }"
    >
      <div class="state-control">
        <h1 class="slice-name">{{ sliceInfo.name }}</h1>
        <div class="toggle-icons-container">
          <IconToggleQA
            :id="sliceInfo.id"
            @toggleVisibility="$emit('toggle-trimmer', sliceInfo.id)"
            iconSource="adjust-trim"
            :status="sliceInfo.imgBarProperties.showTrimmer"
          />
          <IconToggleQA
            :id="sliceInfo.id"
            @toggleVisibility="$emit('toggle-patient-info', sliceInfo.id)"
            iconSource="hide-bottom"
            :status="sliceInfo.imgBarProperties.showPatientInfo"
          />
          <IconToggleQA
            :id="sliceInfo.id"
            :status="sliceInfo.imgBarProperties.showGraph"
            iconSource="show-contours"
            @toggleVisibility="$emit('toggle-graph', sliceInfo.id)"
          />
        </div>
        <div class="state-buttons">
          <StateButtons
            :id="sliceInfo.id"
            :heartrate="stateButtons.FREEBREATHING.heartrate"
            :duration="stateButtons.FREEBREATHING.duration"
            :state="'FREE BREATHING'"
            :is-active="stateButtons.currentState === 'FREEBREATHING'"
            @state-change="handleStateChange"
          />
          <StateButtons
            :id="sliceInfo.id"
            :heartrate="stateButtons.HYPERVENTILATION.heartrate"
            :duration="stateButtons.HYPERVENTILATION.duration"
            :state="'HYPERVENTILATION'"
            :is-active="stateButtons.currentState === 'HYPERVENTILATION'"
            :stateColor="stateButtons.HYPERVENTILATION.stateColor"
            @state-change="handleStateChange"
          />

          <StateButtons
            :id="sliceInfo.id"
            :heartrate="stateButtons.BREATHHOLD.heartrate"
            :duration="stateButtons.BREATHHOLD.duration"
            :state="'BREATHHOLD'"
            :is-active="stateButtons.currentState === 'BREATHHOLD'"
            :stateColor="stateButtons.BREATHHOLD.stateColor"
            @state-change="handleStateChange"
          />
        </div>
      </div>
      <div class="movie-strip">
        <ImageBar
          :id="sliceInfo.sliceOBJ.id"
          :isHidden="sliceInfo.imgBarProperties.showTrimmer"
          :hideinfo="sliceInfo.imgBarProperties.showPatientInfo"
          :slice-obj="sliceInfo.sliceOBJ"
          :currentState="stateButtons.currentState"
          :maxWidth="allowedWidth"
          ref="imgBar"
          @newstate="handleNewState"
          @update-slice="handleUpdateSlice"
          @remove-label="handleRemoveLabel"
          @add-label="handleAddLabel"
          @loaded="loading = false"
        />
      </div>
    </div>
  </Transition>
</template>

<script>
import StateButtons from './StateButtons.vue';
import ImageBar from './ImageBar.vue';
import IconToggleQA from './IconToggleQA.vue';
import SkeletonLoaderCBS from './SkeletonLoaderCBS.vue';

export default {
  components: {
    StateButtons,
    ImageBar,
    IconToggleQA,
    SkeletonLoaderCBS,
  },
  props: {
    sliceInfo: { type: Object },
  },
  data() {
    return {
      stateButtons: this.sliceInfo.stateButtons,
      allowedWidth: null,
      loading: true,
    };
  },
  methods: {
    handleStateChange(state) {
      const cleanState = state.replace(/\s/g, '');
      this.$emit('state-change', cleanState);
      this.$refs.imgBar.scrollToState(cleanState);
    },

    handleNewState(state) {
      this.$emit('state-change', state);
    },
    handleUpdateSlice(newSlice) {
      this.$emit('update-slice', newSlice);
    },
    handleRemoveLabel(obj) {
      this.$emit('remove-label', obj);
    },
    handleAddLabel(obj) {
      this.$emit('add-label', obj);
    },
    handleResize() {
      this.allowedWidth = window.innerWidth - 100;

      if (this.allowedWidth > 2000) {
        this.allowedWidth = 2000;
      }
    },
  },
  created() {
    window.addEventListener('resize', this.handleResize);
    this.handleResize();
  },
};
</script>

<style scoped>
.slice {
  border-radius: 11px;
  background-color: rgba(0, 0, 0, 0.4);
  max-height: 400px;
  display: flex;
  padding: 35px;

  gap: 20px;
}

.slice-name {
  font-size: 24px;
  line-height: 35px;
}
.slices-containers {
  display: flex;
  flex-direction: column;
  gap: 6px;
  /* max-width: 95vw; */
}

.toggle-icons-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.state-buttons {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.movie-strip {
  display: flex;
  flex-direction: column;
}

.slide-fade-enter-active {
  transition: all 0.3s ease-in;
}

.slide-fade-leave-active {
  transition: all 0.2s cubic-bezier(1, 0.5, 0.8, 1);
}

.slide-fade-enter-from,
.slide-fade-leave-to {
  opacity: 0;
}

@media (max-height: 850px) {
  .slice {
    padding-bottom: 10px;
    padding-top: 10px;
  }
}

@media (max-height: 800px) {
  .slice {
    padding-bottom: 5px;
    padding-top: 5px;
  }
}
</style>
