<template>
  <div
    @click="handleclick"
    class="circle2"
    :class="{ clicked: status }"
    @keydown.space.prevent="handleKeyDown"
  >
    <div :class="{ clicked: status }" class="icon-container">
      <img
        :src="'/icons/' + iconSource + '.svg'"
        :style="{ height: iconHeight + 'px' }"
        alt="Icon"
      />
    </div>
  </div>
</template>

<script>
export default {
  props: {
    iconSource: { type: String, default: 'input-cursor' },
    id: { type: Number },
    status: { type: Boolean, default: true },
    iconHeight: { type: Number, default: 30 },
  },
  methods: {
    handleclick() {
      this.$emit('toggleVisibility', this.status);
    },
  },
};
</script>

<style scoped>
.circle2 {
  border-radius: 50px;
  background-color: #00000030;
  height: 48px;
  width: 48px;
  cursor: pointer;
  margin-top: 5px;
  margin-bottom: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s ease-in-out;
}

.circle2:hover {
  background-color: #70707030;
}

.circle2.clicked {
  background-color: #70707040;
}

.icon-container {
  display: flex;
  align-content: center;
  justify-content: center;
  cursor: pointer;
  opacity: 1;
  filter: invert(99%) sepia(93%) saturate(3%) hue-rotate(254deg)
    brightness(106%) contrast(100%);
  transition: all 0.3s ease-in-out;
}

.icon-container.clicked {
  opacity: 1;
  filter: invert(57%) sepia(95%) saturate(558%) hue-rotate(359deg)
    brightness(101%) contrast(107%);
}
</style>
