<script>
import HomeGreetings from '../components/HomeGreetings.vue';
import PatientContainer from '../components/PatientContainer.vue';
// import store from '/src/store/index.js';
import AddPatient from '../components/AddPatient.vue';
import FindPatient from '../components/FindPatient.vue';
import LogOut from '../components/LogOut.vue';

export default {
  data() {
    return {
      username: 'GUEST USER',
      filter: '',
      container: 'container',
      patients: [
        {
          id: 111,
          name: 'SAMPLE',
          lastname: 'SAMPLE',
          dob: '01/02/1982',
          breathingrate: '60',
          age: '32',
          weight: 65,
          height: 175,
          gender: 'Male',
          scandate: '01/02/2023',
          scanlocation: 'Area 19 Office',
        },
      ],
    };
  },
  components: {
    HomeGreetings,
    PatientContainer,
    AddPatient,
    FindPatient,
    LogOut,
  },
  mounted() {
    this.username = window.localStorage.getItem('userLoggedIn');
    window.localStorage.setItem('currentView', 'QualityCheck');
  },
  methods: {
    updatefilter() {
      // store.state.searchFilter = infilter;
    },
    getTimeOfDay() {
      const currentHour = new Date().getHours();

      if (currentHour >= 5 && currentHour < 12) {
        return 'Morning';
      }
      if (currentHour >= 12 && currentHour < 18) {
        return 'Afternoon';
      }
      if (currentHour >= 18 && currentHour < 23) {
        return 'Evening';
      }
      return 'Day';
    },
    handleNewPatient(patient) {
      this.patients.push(patient);
    },
  },
  created() {
    this.timeofday = this.getTimeOfDay();
    this.container = `${this.container} ${this.timeofday.toLowerCase()}`;
  },
};
</script>

