<template>
  <!-- Adding a overlay for smaller screen sizes as wb app is not fully responsive. -->
  <div class="mobile-overlay">
    <div class="warning">
      <h2>
        It appears that you are trying to use our app on a screen size of less
        than 1200 px wide. <br />
        Currently our app is limited to larger devices. Please try on a larger
        laptop.
      </h2>
    </div>
  </div>
  <!-- Main container -->
  <div :class="container">
    <!-- Body container -->
    <div class="body">
      <!-- Navigation Bar section -->
      <div class="navigationBar">
        <!-- AnalysisNavigationBar component to handle view selection (patient list, quality check, analysis, report) -->
        <AnalysisNavigationBar @tool-selected="handleToolSelected" />
      </div>
      <div class="header-main">
        <!-- Header section -->
        <div class="header">
          <!-- PatientHeaderInfo component to display patient information and update quality check view -->
          <PatientHeaderInfo
            :activeComponent="currentComponent"
            @updatequalitycheck="handleUpdateQualityCheck"
          />
        </div>

        <!-- Main section -->
        <div class="main">
          <!-- Use fade transition when switching components -->
          <transition name="fade">
            <!-- Dynamic component based on currentComponent -->
            <component :is="currentComponent" ref="mainAnalysisComponent" />
          </transition>
        </div>

        <!-- Version information -->
        <h5 style="margin-left: auto; color: grey">
          Area 19 OCG Prototype V.0.1
        </h5>
      </div>
    </div>
  </div>
</template>

<script>
import PatientHeaderInfo from '../components/PatientHeaderInfo.vue';
import AnalysisNavigationBar from '../components/AnalysisNavigationBar.vue';
import VtkTwoView from '../components/VtkTwoView.vue';
import QualityCheck from './QualityCheck.vue';
import ReportMaker from './ReportMaker.vue';
import GraphicalAnalysis from './GraphicalAnalysis.vue';

export default {
  components: {
    // Registered Vue components used in this script
    PatientHeaderInfo,
    AnalysisNavigationBar,
    VtkTwoView,
    QualityCheck,
    ReportMaker,
    GraphicalAnalysis,
  },
  data() {
    return {
      // Data properties
      container: 'container',
      currentComponent: 'QualityCheck',
    };
  },
  methods: {
    // Method to get the time of day based on the current hour
    getTimeOfDay() {
      const currentHour = new Date().getHours();

      if (currentHour >= 5 && currentHour < 12) {
        return 'Morning';
      }
      if (currentHour >= 12 && currentHour < 18) {
        return 'Afternoon';
      }
      if (currentHour >= 18 && currentHour < 23) {
        return 'Evening';
      }
      return 'Day';
    },
    // Method to handle tool selection from the AnalysisnavigationBar component
    handleToolSelected(tool) {
      if (tool === 'ReportMaker') {
        this.currentComponent = 'ReportMaker';
      } else if (tool === 'QualityCheck') {
        this.currentComponent = 'QualityCheck';
      } else if (tool === 'GraphicalAnalysis') {
        this.currentComponent = 'GraphicalAnalysis';
      }
      // Save the current view in local storage for persistence
      window.localStorage.setItem('currentView', this.currentComponent);
      // Handle the selected tool, update the displayed content accordingly
    },
    // Method to handle the update of QualityCheck component based on checkContours value
    handleUpdateQualityCheck(checkContours) {
      if (checkContours) {
        this.$refs.mainAnalysisComponent.toggleComponent('CheckContours');
      } else {
        this.$refs.mainAnalysisComponent.toggleComponent(
          'CheckBreathingSplits'
        );
      }
    },
  },
  created() {
    // Set the time of day and update the container class accordingly
    this.timeofday = this.getTimeOfDay();
    this.container = `${this.container} ${this.timeofday.toLowerCase()}`;
  },
  mounted() {
    // Retrieve the last selected view from local storage. Used when user reloads.
    this.currentComponent = window.localStorage.getItem('currentView');
  },
};
</script>

