<template>
  <div class="cards" :style="{ flexDirection: flexDirection }">
    <div
      v-for="item in count"
      :key="item"
      class="card is-loading"
      :style="{
        width: givenWidth + 'px',
        height: givenHeight + 'px',
        borderRadius: radius + 'px',
      }"
    ></div>
  </div>
</template>

<script>
export default {
  props: {
    givenWidth: { type: Number },
    givenHeight: { type: Number },
    count: { type: Number, default: 1 },
    flexDirection: { type: String, default: 'column' },
    radius: { type: Number, default: 5 },
  },
};
</script>

<style>
.cards {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 5px;
}

.card {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.05);
}

.card.is-loading {
  background: #eee;
  background: linear-gradient(
    110deg,
    #79cfde38 8%,
    #7ed7e738 20%,
    #79cfde38 33%
  );
  background-size: 200% 100%;
  animation: shine 1s linear infinite;
}

@keyframes shine {
  to {
    background-position-x: -200%;
  }
}
</style>
