<template>
  <div>
    <canvas ref="canvas"></canvas>
  </div>
</template>

<script>
export default {
  name: 'PatientInfoHUD',
  mounted() {
    const { canvas } = this.$refs;
    canvas.width = this.numImages * this.imgWidth;
    canvas.height = this.imgHeight;

    this.addPhaseId();
    this.addHRInfo();
  },

  data() {
    return {
      paddingX: 15,
    };
  },
  props: {
    isHidden: { type: Boolean },
    numImages: { type: Number, required: true },
    imgWidth: { type: Number, required: true },
    imgHeight: { type: Number, required: true },
  },
  methods: {
    addPhaseId() {
      const { canvas } = this.$refs;
      const context = canvas.getContext('2d');
      context.font = '500 12px Roboto'; // Set the font for the text
      context.textAlign = 'left';
      context.textBaseline = 'middle';
      context.fillStyle = 'white'; // Set the text color to white
      const { numImages } = this;

      const phasePaddingX = this.paddingX;
      const phaseTextY = 15;

      for (let i = 0; i < numImages; i++) {
        context.fillText(
          `Phase ${i + 1}`,
          phasePaddingX + this.imgWidth * i,
          phaseTextY
        );
      }
    },
    addHRInfo() {
      const { canvas } = this.$refs;
      const context = canvas.getContext('2d');

      context.font = '500 12px Roboto'; // Set the font for the text
      context.textAlign = 'left';
      context.textBaseline = 'middle';
      context.fillStyle = 'white'; // Set the text color to white
      const { numImages } = this;

      const phasePaddingX = this.paddingX;
      const phaseTextY = 30;

      for (let i = 0; i < numImages; i++) {
        context.fillText(
          `HR ${this.getRandomInt(106, 30)}`,
          phasePaddingX + this.imgWidth * i,
          phaseTextY
        );
      }
    },

    getRandomInt(max, min) {
      return min + Math.floor(Math.random() * (max - min));
    },
  },
};
</script>
