<template>
  <textarea
    :value="propValue"
    @input="updateProp"
    @blur="stopEditing"
    ref="inputField"
    :class="{ edit: editable, noedit: !editable }"
    :placeholder="placeholder"
    aria-labelledby="textAreaInReportEditable"
  ></textarea>
</template>

<script>
export default {
  props: {
    id: { type: String, required: true },
    propValue: {
      type: String,
      required: true,
      default: '00',
    },
    placeholder: { type: String, default: '' },
    editable: { type: Boolean, default: true },
  },
  data() {
    return {
      editing: 0,
      editedValue: this.propValue,
    };
  },
  methods: {
    stopEditing() {
      this.$emit('updateValue', this.id, this.editedValue);
    },
    updateProp(event) {
      this.editedValue = event.target.value;
    },
  },
  mounted() {
    if (!this.editable) {
      this.$refs.inputField.readOnly = true;
    }
  },
  watch: {
    editable() {
      this.$refs.inputField.readOnly = !this.editable;
    },
  },
};
</script>

<style scoped>
h5 {
  color: #5e5d6c;
  font-weight: bold;
  font-size: 13px;
  height: 17px;
  width: 50px;
}

textarea {
  color: #5e5d6c;
  font-weight: bold;
  font-size: 13px;
  outline: none;
  width: 50px;
  height: 17px;
  overflow: hidden;
  background: none;
  line-height: 15px;
}

textarea.edit {
  border: 1px dashed #229ed7;
  background: none;
  line-height: 18px;
  font-size: 13px;
  color: #5e5d6c;
  transition: all 0.3s ease-in-out;
}

textarea.noedit {
  border: 1px dashed #ffffff00;
  transition: all 0.3s ease-in-out;
  background: none;
  font-weight: bold;
  font-size: 13px;
  line-height: 18px;
  color: #5e5d6c;
}
</style>
