<template>
  <div
    id="image-container"
    :class="{ moving: moveMode, hover: hovering }"
    :style="{ height: height + 'px', width: width + 'pt' }"
    @dragenter="handleDragOver"
    @dragover="handleDragOver"
    @drop="handleImageDrop"
    @dragleave="handleDragLeave"
  >
    <div v-if="editable" class="buttons">
      <div
        class="upload-button"
        title="Upload File"
        alt="Upload File"
        style="position: relative"
      >
        <button class="upload-buttons upload-image" @click="openFilePicker">
          <i class="fas fa-upload fa-lg" style="color: #ffffff"></i>
        </button>
        <input
          type="file"
          ref="imageLoaderInput"
          name="imageLoader"
          style="display: none"
          accept="image/*"
          @change="uploadImage"
          aria-labelledby="hiddenImageInputinReport"
        />
      </div>

      <div
        v-if="originalImage"
        class="move-button"
        title="Move"
        alt="Move"
        style="position: relative"
      >
        <button
          class="upload-buttons"
          :class="{ moving: moveMode }"
          style="left: 50px"
          @click="toggleMoveMode"
        >
          <i class="fas fa-arrows fa-lg" style="color: #ffffff"></i>
        </button>
      </div>

      <div
        v-if="originalImage"
        class="zoom-in-button"
        title="Zoom In"
        alt="Zoom In"
        style="position: relative"
      >
        <button class="upload-buttons" style="left: 90px" @click="zoomIn">
          <i class="fas fa-search-plus fa-lg" style="color: #ffffff"></i>
        </button>
      </div>

      <div
        v-if="originalImage"
        class="zoom-out-button"
        title="Zoom Out"
        alt="Zoom Out"
        style="position: relative"
      >
        <button class="upload-buttons" style="left: 130px" @click="zoomOut">
          <i class="fas fa-search-minus fa-lg" style="color: #ffffff"></i>
        </button>
      </div>

      <!-- <div
        v-if="originalImage"
        class="reset-zoom-button"
        title="Reset Zoom"
        alt="Reset Zoom"
        style="position: relative"
      >
        <button
          class="upload-buttons"
          style="left: 170px"
          @click="resetImagePositionAndZoom"
        >
          <i class="fa-solid fa-arrows-rotate fa-lg" style="color: #ffffff"></i>
        </button>
      </div> -->
    </div>
    <div ref="vascular-image-div">
      <div style="position: relative" v-if="addBar">
        <img
          style="user-select: none; -webkit-user-drag: none"
          id="bar-graph-legend-os"
          ref="barGraphLegend"
          alt="Image Not Loaded"
        />
      </div>
      <div
        :style="{ height: height + 'px', width: width + 'pt' }"
        ref="zoomControl"
      >
        <canvas
          ref="canvas"
          id="vascular-image"
          @mousedown="handleMouseDown"
          @mousemove="handleMouseMove"
          @mouseup="handleMouseUp"
        ></canvas>
      </div>
    </div>
  </div>
</template>

<script>
import html2canvas from 'html2canvas';

export default {
  props: {
    id: { type: String },
    width: { type: Number, default: 200 },
    height: { type: Number, default: 250 },
    canvasText: { type: String, default: 'Select or Drop Image' },
    addBar: { type: String, default: null },
    editable: { type: Boolean, default: true },
  },
  data() {
    return {
      moveMode: false,
      isMoving: false,
      cStartX: null,
      cStartY: null,
      canvasX: 0,
      canvasY: 0,
      zoomValue: 1,
      defaultZoomValue: 1,
      originalImage: null,
      hovering: false,
    };
  },
  methods: {
    setupCanvas() {
      // width = 270, height = 250,
      const { canvas } = this.$refs;
      const ctx = canvas.getContext('2d');
      canvas.width = this.width * (1 / 0.75);
      canvas.height = this.height;

      ctx.fillStyle = 'rgba(0, 0, 0, 0.3)';
      ctx.fillRect(0, 0, canvas.width, canvas.height);

      ctx.fillStyle = 'white';
      ctx.font = '20px Arial';
      ctx.textAlign = 'center';
      ctx.textBaseline = 'middle';

      this.wrapText(
        ctx,
        this.canvasText,
        canvas.width / 2,
        canvas.height / 2,
        150
      );
    },
    wrapText(context, text, x, yi, maxWidth) {
      let y = yi;
      const words = text.split(' ');
      let line = '';
      const lineHeight = context.measureText('M').width * 1.2; // Adjust line height as needed

      for (let i = 0; i < words.length; i++) {
        const testLine = `${line + words[i]} `;
        const testWidth = context.measureText(testLine).width;

        if (testWidth > maxWidth && i > 0) {
          context.fillText(line, x, y);
          line = `${words[i]} `;
          y += lineHeight;
        } else {
          line = testLine;
        }
      }

      context.fillText(line, x, y);
    },
    drawImage() {
      const { canvas } = this.$refs;
      const ctx = canvas.getContext('2d');
      ctx.clearRect(0, 0, canvas.width, canvas.height);
      ctx.drawImage(this.originalImage, 0, 0);
    },
    openFilePicker() {
      this.$refs.imageLoaderInput.click();
    },
    uploadImage(event) {
      const file = event.target.files[0];

      const { canvas } = this.$refs;
      if (file) {
        const reader = new FileReader();
        reader.onload = (e) => {
          this.originalImage = new Image();
          this.originalImage.onload = () => {
            canvas.width = this.originalImage.width + 10;
            canvas.height = this.originalImage.height + 10;
            this.drawImage();
          };
          this.originalImage.src = e.target.result;
          this.updateImageZoom();
          this.resetImagePositionAndZoom();
        };
        reader.readAsDataURL(file);
      }
    },
    uploadImageDrop(file) {
      const { canvas } = this.$refs;
      if (file) {
        const reader = new FileReader();
        reader.onload = (e) => {
          this.originalImage = new Image();
          this.originalImage.onload = () => {
            canvas.width = this.originalImage.width + 10;
            canvas.height = this.originalImage.height + 10;
            this.drawImage();
          };
          this.originalImage.src = e.target.result;
          this.updateImageZoom();
          this.resetImagePositionAndZoom();
        };
        reader.readAsDataURL(file);
      }
    },
    handleImageDrop(event) {
      event.preventDefault();
      const file = event.dataTransfer.files[0];
      this.uploadImageDrop(file);
      this.hovering = false;
    },
    handleDragOver(event) {
      event.preventDefault();

      this.hovering = true;
    },
    handleDragLeave() {
      this.hovering = false;
    },

    toggleMoveMode() {
      if (this.originalImage) {
        this.moveMode = !this.moveMode;
      }
    },
    handleMouseDown(event) {
      if (!this.moveMode) {
        return;
      }
      this.isMoving = true;
      this.cStartX = event.clientX;
      this.cStartY = event.clientY;
    },
    handleMouseMove(event) {
      if (!this.moveMode || !this.isMoving) {
        // Do nothing here.
      } else if (this.cStartX !== undefined && this.cStartY !== undefined) {
        const diffX = event.clientX - this.cStartX;
        const diffY = event.clientY - this.cStartY;
        this.canvasX += diffX;
        this.canvasY += diffY;
        this.$refs.canvas.style.transform = `translate(${this.canvasX}px, ${this.canvasY}px)`;
        this.cStartX = event.clientX;
        this.cStartY = event.clientY;
      }
    },
    handleMouseUp() {
      this.isMoving = false;
      this.cStartX = undefined;
      this.cStartY = undefined;
    },
    zoomIn() {
      this.zoomValue += 0.025;
      this.updateImageZoom();
    },
    zoomOut() {
      this.zoomValue -= 0.025;
      this.updateImageZoom();
    },
    resetImagePositionAndZoom() {
      this.moveMode = false;
      this.zoomValue = this.defaultZoomValue;
      this.updateImageZoom();
    },
    updateImageZoom() {
      if (this.originalImage) {
        const updateDiv = this.$refs.zoomControl;

        // Set the scale value and set origin to top left
        updateDiv.style.transform = `scale(${this.zoomValue})`;
        updateDiv.style.transformOrigin = '0 0';
      }
    },
    async captureScreenshot() {
      // Get the reference to the "vascular-image-div" div
      const divElement = this.$refs['vascular-image-div'];

      // Use html2canvas to capture a screenshot of the div
      html2canvas(divElement, { logging: false, scale: 3 })
        .then((canvas) => {
          // Convert the canvas to an image data URL
          const imageDataURL = canvas.toDataURL('image/png');

          // Emit the image data URL to the parent component
          this.$emit('screenshot-captured', this.id, imageDataURL);
        })
        .catch((error) => {
          console.error('Error capturing screenshot:', error);
        });
    },
  },
  mounted() {
    this.setupCanvas();

    if (this.addBar) {
      const imgTemp = new Image();
      imgTemp.src = this.addBar;
      imgTemp.onload = () => {
        this.$refs.barGraphLegend.src = imgTemp.src;
      };
    }
  },
  watch: {
    editable() {
      if (this.moveMode) {
        this.moveMode = !this.moveMode;
        this.handleMouseUp();
      }
    },
  },
};
</script>

<style scoped>
.upload-buttons {
  cursor: pointer;
  position: absolute;
  background-color: #3a39396e;
  border-radius: 5px;
  border: none;
  display: block;
  width: 30px;
  height: 30px;
  top: 10px;
  z-index: 100;
}

.upload-image {
  left: 10px;
}

.upload-buttons:hover {
  background-color: #229ed7c0;
  transition: 0.3s;
}

#image-container {
  justify-self: center;
  align-self: center;
  background-color: #0000003b;
  overflow: hidden;
}
.hover {
  border: 2px dashed #229ed7;
}

.upload-buttons.moving {
  background-color: #229ed7;
}

#image-container.moving {
  cursor: grab;
}

#bar-graph-legend-os {
  height: 200px;
  position: absolute;
  left: 215px;
  top: 25px;
  z-index: 10;
  background: #5252524b;
  padding: 5px;
  padding-right: 2px;
  border-radius: 10px;
}
</style>
