<template>
  <div>
    <canvas ref="chartCanvas"></canvas>
  </div>
</template>

<script>
export default {
  props: {
    id: {
      type: Number,
      required: true,
    },
  },
  data() {
    return {
      sectionOpacities: [0.7, 0.7, 0.7], // Initial opacity values for each section
      hoveredSection: -1,
      outerradius: 60,
    };
  },
  mounted() {
    this.createChart();
  },
  methods: {
    createChart() {
      const canvas = this.$refs.chartCanvas;
      const ctx = canvas.getContext('2d');

      const centerX = canvas.width / 2 - 85;
      const centerY = canvas.height / 2;
      const outerRadius = this.outerradius;
      const innerRadius = outerRadius / 3;

      ctx.clearRect(0, 0, canvas.width, canvas.height);
      canvas.addEventListener('click', this.handleChartClick);

      canvas.addEventListener('mousemove', this.handleChartHover);
      canvas.addEventListener('mouseleave', this.handleChartLeave);
      canvas.addEventListener('dblclick', this.handleChartDoubleClick);

      // Customize the colors for each section
      const sectionColors = ['#a2191a', '#105a93', '#1f7c55'];

      // Define the data for the chart sections
      const chartData = [1, 1, 1]; // Equal values for each section

      const totalValue = chartData.reduce((total, value) => total + value, 0);

      let startAngle = 0;
      chartData.forEach((value, index) => {
        const sectionAngle = (value / totalValue) * Math.PI * 2;
        const endAngle = startAngle + sectionAngle;

        ctx.beginPath();
        ctx.moveTo(
          centerX + outerRadius * Math.cos(startAngle),
          centerY + outerRadius * Math.sin(startAngle)
        );
        ctx.arc(centerX, centerY, outerRadius, startAngle, endAngle);
        ctx.arc(centerX, centerY, innerRadius, endAngle, startAngle, true);
        ctx.closePath();

        // Apply opacity to the section
        const opacity =
          this.hoveredSection === index
            ? this.sectionOpacities[index] + 0.3
            : this.sectionOpacities[index];
        ctx.globalAlpha = opacity;

        ctx.fillStyle = sectionColors[index];
        ctx.fill();

        // Reset globalAlpha back to 1
        ctx.globalAlpha = 1;

        // Add stroke between the regions
        ctx.strokeStyle = '#ffffff';
        ctx.lineWidth = 2;
        ctx.stroke();

        startAngle = endAngle;
      });

      // Draw white stroke around the outer circle
      ctx.beginPath();
      ctx.arc(centerX, centerY, outerRadius, 0, Math.PI * 2);
      ctx.strokeStyle = '#ffffff';
      ctx.lineWidth = 1;
      ctx.stroke();

      // Draw white stroke around the inner hole
      ctx.beginPath();
      ctx.arc(centerX, centerY, innerRadius, 0, Math.PI * 2);
      ctx.strokeStyle = '#ffffff';
      ctx.lineWidth = 1;
      ctx.stroke();
    },
    handleChartClick(event) {
      const canvas = this.$refs.chartCanvas;
      const rect = canvas.getBoundingClientRect();
      const mouseX = event.clientX - rect.left;
      const mouseY = event.clientY - rect.top;

      const centerX = canvas.width / 2 - 85;
      const centerY = canvas.height / 2;
      const outerRadius = this.outerradius;
      const innerRadius = outerRadius / 3;

      const distanceFromCenter = Math.sqrt(
        (mouseX - centerX) ** 2 + (mouseY - centerY) ** 2
      );

      if (distanceFromCenter < innerRadius) {
        // Clicked on the inner hole, restore all opacities
        this.sectionOpacities = [0.7, 0.7, 0.7];
      } else if (distanceFromCenter >= outerRadius) {
        return;
      } else {
        const angle = Math.atan2(mouseY - centerY, mouseX - centerX);
        const normalizedAngle = angle >= 0 ? angle : angle + Math.PI * 2;

        const sectionIndex = Math.floor((normalizedAngle / (Math.PI * 2)) * 3);

        // Toggle opacity for the clicked section
        this.sectionOpacities[sectionIndex] =
          this.sectionOpacities[sectionIndex] === 0.7 ? 0.2 : 0.7;
      }

      // Redraw the chart with updated opacities
      //   this.$emit('segment-updated', this.id, this.sectionOpacities);
      this.createChart();
      this.$emit('segment-updated', this.id, this.sectionOpacities);
    },

    handleChartDoubleClick(event) {
      const canvas = this.$refs.chartCanvas;
      const rect = canvas.getBoundingClientRect();
      const mouseX = event.clientX - rect.left;
      const mouseY = event.clientY - rect.top;

      const centerX = canvas.width / 2 - 85;
      const centerY = canvas.height / 2;
      const outerRadius = this.outerradius;
      const innerRadius = outerRadius / 3;

      const distanceFromCenter = Math.sqrt(
        (mouseX - centerX) ** 2 + (mouseY - centerY) ** 2
      );

      if (distanceFromCenter < innerRadius) {
        // Clicked on the inner hole, restore all opacities
        this.sectionOpacities = [0.7, 0.7, 0.7];
      } else if (distanceFromCenter >= outerRadius) {
        return;
      } else {
        const angle = Math.atan2(mouseY - centerY, mouseX - centerX);
        const normalizedAngle = angle >= 0 ? angle : angle + Math.PI * 2;

        const sectionIndex = Math.floor((normalizedAngle / (Math.PI * 2)) * 3);

        // Toggle opacity for the clicked section
        this.sectionOpacities = [0.2, 0.2, 0.2];
        this.sectionOpacities[sectionIndex] = 0.7;
      }

      // Redraw the chart with updated opacities
      //   this.$emit('segment-updated', this.id, this.sectionOpacities);
      this.createChart();
      this.$emit('segment-updated', this.id, this.sectionOpacities);
    },

    handleChartHover(event) {
      const canvas = this.$refs.chartCanvas;
      const rect = canvas.getBoundingClientRect();
      const mouseX = event.clientX - rect.left;
      const mouseY = event.clientY - rect.top;

      const centerX = canvas.width / 2 - 85;
      const centerY = canvas.height / 2;
      const outerRadius = this.outerradius;
      const innerRadius = outerRadius / 3;

      const distanceFromCenter = Math.sqrt(
        (mouseX - centerX) ** 2 + (mouseY - centerY) ** 2
      );

      if (distanceFromCenter < innerRadius) {
        // Hovered on the inner hole, restore all opacities
        this.hoveredSection = -1;
      } else if (distanceFromCenter >= outerRadius) {
        this.handleChartLeave();
        return;
      } else {
        const angle = Math.atan2(mouseY - centerY, mouseX - centerX);
        const normalizedAngle = angle >= 0 ? angle : angle + Math.PI * 2;

        const sectionIndex = Math.floor((normalizedAngle / (Math.PI * 2)) * 3);

        // Update the hovered section
        this.hoveredSection = sectionIndex;
      }

      // Redraw the chart with updated opacities
      this.createChart();
    },
    handleChartLeave() {
      // Reset the hovered section when leaving the chart
      this.hoveredSection = -1;

      // Redraw the chart with updated opacities
      this.createChart();
    },
  },
};
</script>

