<template>
  <div
    :class="{ 'custom-button': true, highlighted: isActive }"
    @click="handleClick"
    @mouseover="isHovered = true"
    @focusin="isHovered = true"
    @mouseout="isHovered = false"
    @focusout="isHovered = false"
    @keydown.space.prevent="handleKeyDown"
  >
    <div class="button-content">
      <div>
        <h1 :style="{ color: stateColor }">{{ state }}</h1>
      </div>
      <div style="display: flex; justify-content: space-between">
        <div>
          <h2>{{ heartrate }} BPM</h2>
          <h4>HEART RATE</h4>
        </div>
        <div>
          <h2>{{ duration }}</h2>
          <h4>DURATION</h4>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    heartrate: {
      type: Number,
      required: true,
    },
    duration: {
      type: String,
      required: true,
    },
    state: {
      type: String,
      required: true,
    },
    isActive: {
      type: Boolean,
      required: true,
    },
    stateColor: {
      type: String,
      required: false,
      default: '#66C9DA',
    },
  },
  data() {
    return {
      isHovered: false,
    };
  },
  methods: {
    handleClick() {
      this.$emit('state-change', this.state);
    },
  },
};
</script>

