<template>
  <div>
    <h4
      @mouseenter="handleMouseEnter"
      @focusin="handleMouseEnter"
      @mouseleave="handleMouseLeave"
      @focusout="handleMouseLeave"
      @click="handleClick"
      :style="{ opacity: setTextOpacity }"
      @keydown.space.prevent="handleKeyDown"
    >
      {{ label }}
    </h4>
  </div>
</template>

<script>
export default {
  props: {
    label: {
      type: String,
      default: 'Territory Min',
    },
    textOpacity: {
      type: Number,
      default: 1,
    },
  },
  updated() {},

  data() {
    return {
      setTextOpacity: this.textOpacity,
    };
  },
  methods: {
    handleMouseEnter() {
      // Function to call when hovering over the div
      this.setTextOpacity += 0.3;
      // Call your desired function or perform any action
    },
    handleMouseLeave() {
      // Function to call when hovering out of the div
      this.setTextOpacity -= 0.3;
      // Call your desired function or perform any action
    },
    handleClick() {
      // Function to call when hovering out of the div
      // if (this.setTextOpacity <= 0.6) {
      //   this.setTextOpacity = 0.9;
      // } else {
      //   this.setTextOpacity = 0.6;
      // }

      this.$emit('biomarker-click', this.label);

      // Call your desired function or perform any action
    },
  },
};
</script>

<style scoped>
h4 {
  font-size: 14px;
}

