<template>
  <div>
    <canvas ref="canvas" width="411" height="206" style=""></canvas>
  </div>
</template>

<script>
export default {
  props: {
    dataToGraph: {
      type: Array,
    },
  },

  watch: {
    dataToGraph: {
      deep: true,
      handler() {
        // console.log('change');
        // console.log(this.dataToGraph);

        this.drawLines();
        // myLineChart = new LineChart(con);
      },
    },
  },

  data() {
    return {
      canvasId: 'myCanvas',

      // user defined properties
      minX: 0,
      minY: 0,
      maxX: 15,
      maxY: 1,
      unitsPerTickX: 5,
      unitsPerTickY: 0.25,
      title: 'Normalized SI (RV-LV)',

      // constants
      padding: 15,
      tickSize: 0.1,
      axisColor: 'white',
      pointRadius: 1,
      font: '10pt Arial',
      fontHeight: 10,
      rangeX: null,
      rangeY: null,
      numXTicks: null,
      numYTicks: null,
      x: null,
      y: null,
      width: null,
      height: null,
      scaleX: null,
      scaleY: null,

      context: null,
      data: null,
    };
  },

  methods: {
    drawSysDias(contexti, xDiastole) {
      const context = contexti;
      context.save();
      context.translate(this.x, this.y + this.height);

      context.font = '12px Arial';
      context.fillStyle = 'white';

      context.fillText(
        'Systole',
        0 * this.scaleX,
        0 - (this.height + this.padding)
      );

      // Create gradient
      // console.log(this.scaleX);
      const grd = context.createLinearGradient(
        xDiastole * this.scaleX,
        0,
        this.maxX * this.scaleX - 50,
        0
      );
      grd.addColorStop(0, 'rgba(38, 151, 192, 0.25)');
      grd.addColorStop(1, '#686F7100');

      // Fill with gradient
      context.fillStyle = grd;
      context.fillRect(
        xDiastole * this.scaleX,
        0,
        this.maxX * this.scaleX,
        0 - (this.height + 2 * this.padding)
      );
      context.strokeStyle = 'white';
      context.lineWidth = 1;
      context.beginPath();
      context.moveTo(xDiastole * this.scaleX, 0);
      context.lineTo(
        xDiastole * this.scaleX,
        0 - (this.height + 2 * this.padding)
      );
      context.stroke();

      context.fillStyle = 'white';

      context.fillText(
        'Diastole',
        xDiastole * this.scaleX + 5,
        0 - (this.height + this.padding)
      );
      context.restore();
    },

    getLongestValueWidth(contexti) {
      let longestValueWidth = 0;
      const context = contexti;
      context.font = this.font;

      for (let n = 0; n <= this.numYTicks; n++) {
        const value = this.maxY - n * this.unitsPerTickY;
        longestValueWidth = Math.max(
          longestValueWidth,
          context.measureText(value).width
        );
      }
      return longestValueWidth;
    },

    drawXAxis(contexti) {
      const context = contexti;
      context.save();
      context.beginPath();
      context.moveTo(this.x, this.y + this.height);
      // context.lineTo(this.x + this.width, this.y + this.height);
      context.strokeStyle = this.axisColor;
      context.lineWidth = 1;
      context.stroke();

      // draw tick marks (optional)
      // for (var n = 0; n < this.numXTicks; n++) {
      //   context.beginPath();
      //   context.moveTo(
      //     ((n + 1) * this.width) / this.numXTicks + this.x,
      //     this.y + this.height
      //   );
      //   context.lineTo(
      //     ((n + 1) * this.width) / this.numXTicks + this.x,
      //     this.y + this.height - this.tickSize
      //   );
      //   context.stroke();
      // }

      // draw labels
      context.font = this.font;
      context.fillStyle = 'white';
      context.textAlign = 'center';
      context.textBaseline = 'middle';

      // Draw x axis
      for (let n = -1; n < this.numXTicks; n++) {
        const label = Math.round(((n + 1) * this.maxX) / this.numXTicks);
        context.save();
        context.translate(
          ((n + 1) * this.width) / this.numXTicks + this.x,
          this.y + this.height + this.padding
        );
        context.fillText(label, 0, 0);
        context.restore();
      }
      context.restore();
    },

    drawYAxis(contexti) {
      const context = contexti;
      context.save();
      context.save();
      context.beginPath();
      context.moveTo(this.x, this.y);
      // context.lineTo(this.x, this.y + this.height);
      context.strokeStyle = this.axisColor;
      // context.lineWidth = 0.5;
      context.stroke();
      context.restore();

      // draw values
      context.font = this.font;
      context.fillStyle = 'white';
      context.textAlign = 'right';
      context.textBaseline = 'middle';

      for (let n = 0; n <= this.numYTicks; n++) {
        const value = this.maxY - (n * this.maxY) / this.numYTicks;
        context.save();
        context.translate(
          this.x - this.padding + 5,
          (n * this.height) / this.numYTicks + this.y
        );
        context.fillText(value, 0, 0);
        context.restore();
      }
      context.restore();
    },

    drawLine(contexti, data, color, width) {
      const context = contexti;
      context.save();
      this.transformContext(context);
      context.lineWidth = width;
      context.strokeStyle = color;
      context.fillStyle = color;
      context.shadowBlur = 10;
      context.shadowColor = color;

      context.beginPath();
      context.moveTo(data[0].x * this.scaleX, data[0].y * this.scaleY);

      for (let n = 0; n < data.length; n++) {
        const point = data[n];

        // draw segment
        context.lineTo(point.x * this.scaleX, point.y * this.scaleY);
        context.stroke();
        context.closePath();
        context.beginPath();
        context.arc(
          point.x * this.scaleX,
          point.y * this.scaleY,
          this.pointRadius,
          0,
          2 * Math.PI,
          false
        );
        context.fill();
        context.closePath();

        // position for next segment
        context.beginPath();
        context.moveTo(point.x * this.scaleX, point.y * this.scaleY);
      }
      context.restore();
    },
    transformContext(context) {
      // move context to center of canvas
      context.translate(this.x, this.y + this.height);

      // invert the y scale so that increments
      // as you move upwards
      context.scale(1, -1);
    },
    generateRandomData(n) {
      const data = [];

      for (let i = 0; i <= n; i++) {
        const x = i * 1; // Increasing x-values by increments of 20
        const y = Math.random(); // Random y-value between 0 and 1

        data.push({ x, y });
      }

      return data;
    },

    drawLines() {
      const { context } = this;

      context.clearRect(
        0 + this.padding,
        this.y - 2 * this.padding,
        this.width + 4 * this.padding,
        this.height + 4 * this.padding
      );
      const colors = ['#a2191a', '#105a93', '#1f7c55'];

      for (let i = 0; i < this.dataToGraph.length; i++) {
        if (this.dataToGraph[i] === 1) {
          this.drawLine(context, this.data[i], colors[i], 2);
        }
      }
      this.drawSysDias(context, 7.5);
      this.drawXAxis(context);
      this.drawYAxis(context);
    },
  },
  mounted() {
    const { canvas } = this.$refs;
    const context = canvas.getContext('2d');
    this.x = this.getLongestValueWidth(context) + this.padding * 2 + 25;
    this.context = context;
    this.y = this.padding * 2;
    this.width = canvas.width - this.x - this.padding * 2;
    this.height = canvas.height - this.y - this.padding - this.fontHeight;
    this.rangeX = this.maxX - this.minY;
    this.scaleX = this.width / this.rangeX;
    this.rangeY = this.maxY - this.minY;
    this.scaleY = this.height / this.rangeY;

    this.numXTicks = Math.round(this.rangeX / this.unitsPerTickX);
    this.numYTicks = Math.round(this.rangeY / this.unitsPerTickY);

    // draw x y axis and tick marks
    this.drawXAxis(context);
    this.drawYAxis(context);

    // Save the current transformation state
    context.save();

    // Set the rotation angle and position of the text
    context.translate(10, this.height / 1.5);
    context.rotate((270 * Math.PI) / 180);

    // Set the text properties
    context.font = '12px Arial';
    context.fillStyle = 'white';
    context.textAlign = 'center';
    context.textBaseline = 'middle';

    // Draw the rotated text
    context.fillText(this.title, 0, 0);

    // Restore the original transformation state
    context.restore();

    this.drawSysDias(context, 7.5);

    // const myLineChart = new LineChart(this.con);

    // const redData = [
    //   { x: 20, y: 0.85 },
    //   { x: 40, y: 0.75 },
    //   { x: 60, y: 0 },
    //   { x: 80, y: 0 },
    //   { x: 100, y: 0 },
    //   { x: 120, y: 0.4 },
    //   { x: 150, y: 0.35 },
    // ];
    const dataHere = [
      this.generateRandomData(15),
      this.generateRandomData(15),
      this.generateRandomData(15),
    ];

    this.data = dataHere;

    this.drawLines();
  },
};
</script>

<style scoped>
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}

.fade-enter,
.fade-leave-to {
  opacity: 0;
}
</style>
