<script>
import html2canvas from 'html2canvas';
import jsPDF from 'jspdf';
import ReportLogoHolder from './ReportLogoHolder.vue';
import ReportDropDown from './ReportDropDown.vue';
import ReportImageContainer from './ReportImageContainer.vue';
import ReportStrainMaps from './ReportStrainMaps.vue';
import ReportTextBox from './ReportTextBox.vue';
import ReportTextBoxMultiple from './ReportTextBoxMultiple.vue';
// import VueHtml2pdf from 'vue-html2pdf'

export default {
  components: {
    ReportLogoHolder,
    ReportDropDown,
    ReportImageContainer,
    ReportStrainMaps,
    ReportTextBox,
    ReportTextBoxMultiple,
  },
  props: {
    editable: { type: Boolean, required: true, default: true },
  },
  data() {
    return {
      genderOptions: [
        { value: 'Female', label: 'Female' },
        { value: 'Male', label: 'Male' },
        { value: 'Other', label: 'Other' },
      ],
      completeDataOptions: [
        { value: 'Yes', label: 'Yes' },
        { value: 'No', label: 'No' },
      ],
      imageQualityOptions: [
        { value: 'Excellent', label: 'Excellent' },
        { value: 'Good', label: 'Good' },
        { value: 'OK', label: 'OK' },
        { value: 'Bad', label: 'Bad' },
      ],

      OS_REPORT_VALUES: {
        reportTitle: 'BE-OS CMR Report',
        reportLogo: null,

        patientName: 'PATIENT NAME',
        patientGender: '',
        patientAge: '00',
        patientDOB: 'DD-Mon-YYYY',
        patientID: '123 456 789',

        // scanID: "",
        rbhNumber: '000 000 000',
        protocol: 'OS C-',
        scanDate: 'DD-Mon-YYYY',
        scanTime: '00:00',

        completeData: '',
        imageQuality: '',

        reader: 'Reader Name, MD',
        verifier: 'Verifier Name, MD',

        scannerSystem: 'Philips Ingenia',
        fieldStrength: '1.5T',
        softwareVersion: '5.7.1.3',
        starClaimer:
          '*Normal values are continuously being updated, therefore, may be subject to changes',

        summary: '',

        heartRateBaseline: '00',
        heartRateHV: '00',
        heartRateBH: '00',
        dtheartrate: '00',
        dtheartrateNormalRange: '>15',

        vascularGlobal: '00',
        vascularLAD: '00',
        vascularLCX: '00',
        vascularRCA: '00',
        normalRangeVascular: '>9.1',

        strainLADBaseline: '00',
        strainLADHV: '00',
        strainLADBH: '00',

        strainLCXBaseline: '00',
        strainLCXHV: '00',
        strainLCXBH: '00',

        strainRCABaseline: '00',
        strainRCAHV: '00',
        strainRCABH: '00',

        strainGLOBALBaseline: '00',
        strainGLOBALHV: '00',
        strainGLOBALBH: '00',

        vascularImage: '',
        strainImage: '',

        oxygenSensitiveMap: '',
        strainMapImage: '',

        strainMapBaseline: '',
        strainMapHV: '',
        strainMapBH: '',
        normalRange: '<-20%',
      },
    };
  },
  methods: {
    onOptionSelected(id, value) {
      this.OS_REPORT_VALUES[id] = value;
    },
    handleLogoUpdate(newLogo) {
      this.OS_REPORT_VALUES.reportLogo = newLogo;
    },
    handleUpdateValues(id, newValue) {
      // Update the 'values[id]' with the new content when the user edits the field
      this.OS_REPORT_VALUES[id] = newValue;
    },
    downloadReport() {
      this.buildPDF();
    },
    async getImage(id) {
      await this.$refs[id].captureScreenshot();
    },

    async getImages() {
      await this.getImage('strainImage');
      await this.getImage('vascularImage');
      await this.getImage('oxygenSensitiveMap');
      await this.captureScreenshot('strainMapImage', 'territorymapimg');
      await this.captureScreenshot('reportLogo', 'reportLogo');
    },
    async captureScreenshot(id, element) {
      // Get the reference to the "vascular-image-div" div
      const divElement = this.$refs[element];

      // Use html2canvas to capture a screenshot of the div
      html2canvas(divElement, { logging: false, scale: 3 })
        .then((canvas) => {
          // Convert the canvas to an image data URL
          const imageDataURL = canvas.toDataURL('image/png');

          // Emit the image data URL to the parent component
          this.handleImageScreenshot(id, imageDataURL);
        })
        .catch((error) => {
          console.error('Error capturing screenshot:', error);
        });
    },

    handleImageScreenshot(id, image) {
      this.OS_REPORT_VALUES[id] = image;
    },
    buildPDF() {
      const fileOBJ = this.OS_REPORT_VALUES;
      // const fileOBJ = JSON.parse(window.localStorage.getItem('reportValues'));
      window.jsPDF = window.jspdf.jsPDF;
      // Document of 210mm wide and 297mm high
      const doc = jsPDF('p', 'pt', [612, 792]);
      doc.addFont('/fonts/Roboto-Light.ttf', 'Roboto', 'normal');
      doc.addFont('/fonts/arial_italic.ttf', 'Arial', 'normal');
      doc.addFont('/fonts/arial_greek.ttf', 'ArialGreek', 'normal');
      // doc.addFont('arial_bold.ttf', 'Arial', 'normal');

      doc.setFont('helvetica', 'bold');
      doc.setFontSize(22);
      doc.text(fileOBJ.reportTitle, 42, 60);

      doc.setFontSize(9);
      doc.setTextColor('#5E5D6C');
      doc.text('Patient', 42, 80);

      if (fileOBJ.reportLogo != null) {
        doc.addImage(fileOBJ.reportLogo, 'PNG', 413, 25, 150, 52.5);
      }

      doc.setFontSize(15);
      doc.setTextColor('#229ED7');
      doc.text(fileOBJ.patientName, 42, 97);

      doc.setFontSize(9);
      doc.setTextColor('#5E5D6C');
      doc.text('Gender:', 43, 114);

      doc.setFontSize(10);
      doc.setTextColor('#000917');
      doc.text(fileOBJ.patientGender, 81, 114);

      doc.setFontSize(9);
      doc.setTextColor('#5E5D6C');
      doc.text('Age:', 164, 114);

      doc.setFontSize(10);
      doc.setTextColor('#000917');
      doc.text(fileOBJ.patientAge, 187, 114);

      doc.setFontSize(9);
      doc.setTextColor('#5E5D6C');
      doc.text('DOB:', 269, 114);

      doc.setFontSize(10);
      doc.setTextColor('#000917');
      doc.text(fileOBJ.patientDOB, 294, 114);

      doc.setFontSize(9);
      doc.setTextColor('#5E5D6C');
      doc.text('Patient ID:', 410, 114);

      doc.setFontSize(10);
      doc.setTextColor('#000917');
      doc.text(fileOBJ.patientID, 460, 114);

      doc.setLineWidth(0.1);
      doc.line(577, 120, 35, 122); // horizontal line

      // Below is Exam Data:
      doc.setFontSize(10);
      doc.setTextColor('#5E5D6C');
      doc.text('Exam', 42, 148);

      doc.setFontSize(9);
      doc.setTextColor('#5E5D6C');
      doc.text('RBH number:', 42, 162);

      doc.setFontSize(9);
      doc.setTextColor('#000917');
      doc.text(fileOBJ.rbhNumber, 42, 175);

      doc.setFontSize(9);
      doc.setTextColor('#5E5D6C');
      doc.text('Protocol:', 42, 189);

      doc.setFontSize(9);
      doc.setTextColor('#000917');
      doc.text(fileOBJ.protocol, 42, 202);

      doc.setFontSize(9);
      doc.setTextColor('#5E5D6C');
      doc.text('Date:', 42, 216);

      doc.setFontSize(9);
      doc.setTextColor('#000917');
