<template>
  <div class="reportcontainermain">
    <div class="reporthere">
      <!-- <div class="hidereport">
                <h1>hello</h1>
            </div> -->

      <Report ref="editablereport" :editable="edit" />
    </div>

    <div class="toolbar">
      <button class="tool" @click="handleEdit" :class="{ clicked: edit }">
        <h1>Edit</h1>
      </button>
      <button class="tool"><h1>Share</h1></button>
      <button class="tool" @click="printReport">
        <h1>Print</h1>
      </button>
      <div style="position: relative">
        <button class="tool" @click="handleSave"><h1>Save</h1></button>
        <div class="popup" :class="{ show: showPopup }">
          <h5>Please stop editing first.</h5>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Report from '../components/ReportHtml.vue';

export default {
  components: {
    Report,
  },
  methods: {
    printReport() {
      this.$refs.editablereport.buildPDF();
    },
    handleEdit() {
      this.edit = !this.edit;
    },
    handleSave() {
      if (this.edit) {
        // Show the popup for 3 seconds
        this.showPopup = true;
        setTimeout(() => {
          this.showPopup = false;
        }, 3000); // 3000 milliseconds = 3 seconds
      } else {
        this.$refs.editablereport.downloadReport();
      }
    },
  },
  data() {
    return {
      edit: true,
      showPopup: false,
    };
  },
};
</script>

