<template>
  <div class="range-slider" :style="{ width: canvasWidth + 'px' }">
    <canvas
      ref="canvas"
      @mousedown="handleMouseDown"
      @dblclick="handledblCkick"
      @mousemove="handleMouseMove"
      @mouseup="handleMouseUp"
      @mouseleave="handleMouseUp"
      @focusout="handleMouseUp"
      :style="{ cursor: canvasCursor }"
    ></canvas>
  </div>
</template>

<script>
import throttle from 'lodash.throttle';

export default {
  name: 'RangeSlider',
  props: {
    maxExpand: { type: Number, default: 10 },
    minExpand: { type: Number, default: 3 },
    canvasWidth: { type: Number },

    sliceObj: { type: Object },
    numImages: Number,
    imgWidth: Number,
  },

  data() {
    return {
      canvasHeight: 44,
      thumbRadius: 3,
      isDragging: false,
      startThumbX: 0,
      endThumbX: 0,
      minValue: 0,
      maxValue: 100,
      startValue: 0,
      startValueParent: 0,
      endValue: 10,
      startDragX: null,
      scrollerHeight: 12,
      canvasCursor: 'pointer',
    };
  },
  mounted() {
    this.endValue =
      1 / ((this.imgWidth * this.numImages) / (this.canvasWidth - 8) / 100);
    console.log(`IMAGE STRIP WIDTH: ${this.canvasWidth}`);

    this.drawCanvas();
    this.throttledSliderMove = throttle(this.moveScale, 5);
  },
  methods: {
    drawCurrentView(startX, endX) {
      const x = startX;
      const width = Math.abs(endX - startX);
      const { canvas } = this.$refs;
      const ctx = canvas.getContext('2d');
      // ctx.strokeStyle = obj.stroke;
      ctx.strokeStyle = '#FFFFFF';
      // ctx.fillStyle = obj.color;
      ctx.fillStyle = '#FFFFFF05';
      const height = 35;
      const y = (this.canvasHeight - height) / 2;

      ctx.lineWidth = 3;
      const cornerRadius = 6;

      ctx.beginPath();
      ctx.moveTo(x + cornerRadius, y);
      ctx.lineTo(x + width - cornerRadius, y);
      ctx.arcTo(x + width, y, x + width, y + cornerRadius, cornerRadius);
      ctx.lineTo(x + width, y + height - cornerRadius);
      ctx.arcTo(
        x + width,
        y + height,
        x + width - cornerRadius,
        y + height,
        cornerRadius
      );
      ctx.lineTo(x + cornerRadius, y + height);
      ctx.arcTo(x, y + height, x, y + height - cornerRadius, cornerRadius);
      ctx.lineTo(x, y + cornerRadius);
      ctx.arcTo(x, y, x + cornerRadius, y, cornerRadius);
      ctx.closePath();

      ctx.fill(); // Fill the rectangle with red
      ctx.stroke(); // Draw the stroke around the rectangle
    },
    drawHandle(ctx, xStart, yStart, width, height, borderRadius, color) {
      const context = ctx;
      const xEnd = xStart + width;
      const yEnd = yStart + height;
      // Draw rounded rectangle
      context.beginPath();
      context.moveTo(xStart + borderRadius, yStart);
      context.lineTo(xEnd - borderRadius, yStart);
      context.quadraticCurveTo(xEnd, yStart, xEnd, yStart + borderRadius);
      context.lineTo(xEnd, yEnd - borderRadius);
      context.quadraticCurveTo(xEnd, yEnd, xEnd - borderRadius, yEnd);
      context.lineTo(xStart + borderRadius, yEnd);
      context.quadraticCurveTo(xStart, yEnd, xStart, yEnd - borderRadius);
      context.lineTo(xStart, yStart + borderRadius);
      context.quadraticCurveTo(xStart, yStart, xStart + borderRadius, yStart);
      context.closePath();

      context.lineWidth = 2; // Border width
      context.strokeStyle = color; // Border color
      context.stroke();

      // Fill the rectangle with a color
      context.fillStyle = color;
      context.fill();
    },
    drawSegment(startX, endX, color) {
      const { canvas } = this.$refs;
      const ctx = canvas.getContext('2d');

      const verticalSpace = (this.canvasHeight - 32) / 2;
      const distance = Math.abs(endX - startX);

      this.drawHandle(ctx, startX, verticalSpace, 8, 32, 4, color);
      // Draw rest of track ahead:
      ctx.fillStyle = color;
      ctx.fillRect(
        startX,
        canvas.height / 2 - this.scrollerHeight / 2,
        distance,
        this.scrollerHeight
      );
      this.drawHandle(ctx, endX, verticalSpace, 8, 32, 4, color);
    },

    drawTrack() {
      const { canvas } = this.$refs;
      const ctx = canvas.getContext('2d');
      // Draw track:
      ctx.fillStyle = '#3D3D3D';
      ctx.fillRect(
        0,
        canvas.height / 2 - this.scrollerHeight / 2,
        canvas.width,
        this.scrollerHeight
      );
    },
    drawCanvas() {
      const { canvas } = this.$refs;
      const ctx = canvas.getContext('2d');

      canvas.width = this.canvasWidth - 8;
      canvas.height = this.canvasHeight;

      const startThumbX = this.getValueToXPosition(this.startValue);
      const endThumbX = this.getValueToXPosition(this.endValue);

      const startFtoX = this.getObjLocToVal(
        this.sliceObj.states.FREEBREATHING.start
      );

      const endFtoX = this.getObjLocToVal(
        this.sliceObj.states.FREEBREATHING.end
      );

      const startHtoX = this.getObjLocToVal(
        this.sliceObj.states.HYPERVENTILATION.start
      );

      const endHtoX = this.getObjLocToVal(
        this.sliceObj.states.HYPERVENTILATION.end
      );

      const startBHtoX = this.getObjLocToVal(
        this.sliceObj.states.BREATHHOLD.start
      );

      const endBHtoX = this.getObjLocToVal(this.sliceObj.states.BREATHHOLD.end);

      ctx.clearRect(0, 0, canvas.width, canvas.height);

      // // Draw track
      this.drawTrack();

      // Draw state positions:

      this.drawSegment(startFtoX, endFtoX - 7, '#5FBBCB');
      this.drawSegment(startHtoX + 3, endHtoX - 7, '#C24ABC');
      this.drawSegment(startBHtoX + 3, endBHtoX - 7, '#D95476');

      this.drawCurrentView(startThumbX, endThumbX);
    },

    getObjLocToVal(obj) {
      const endF = obj.XLocation / (this.imgWidth * this.numImages);
      const endFtoX = this.getValueToXPosition(endF * 100);

      return endFtoX;
    },

    getValueToXPosition(value) {
      const range = this.maxValue - this.minValue;
      const width = this.canvasWidth - 8 - this.thumbRadius * 2;
      return ((value - this.minValue) * width) / range + this.thumbRadius;
    },
    getXPositionToValue(x) {
      const range = this.maxValue - this.minValue;
      const width = this.canvasWidth - 8 - this.thumbRadius * 2;
      return ((x - this.thumbRadius) * range) / width + this.minValue;
    },
    handleMouseDown(event) {
      const { canvas } = this.$refs;
      const rect = canvas.getBoundingClientRect();
      const mouseX = event.clientX - rect.left;

      const startThumbX = this.getValueToXPosition(this.startValue);
      const endThumbX = this.getValueToXPosition(this.endValue);

      this.startDragX = mouseX;

      if (mouseX < endThumbX && mouseX > startThumbX) {
        this.isDragging = 'middle';
        this.canvasCursor = 'grab';
      }
    },
    handledblCkick(event) {
      const { canvas } = this.$refs;
      const rect = canvas.getBoundingClientRect();
      const mouseX = event.clientX - rect.left;

      const widthWindow = Math.abs(this.startValue - this.endValue);

      const newStart = Math.min(
        Math.max(this.getXPositionToValue(mouseX) - widthWindow / 2, 0),
        this.maxValue - widthWindow
      );

      this.startValue = newStart;
      this.endValue = this.startValue + widthWindow;

      this.throttledSliderMove(this.startValue);
      this.drawCanvas();
    },
    handleMouseMove(event) {
      if (!this.isDragging) {
        return;
      }

      const { canvas } = this.$refs;
      const rect = canvas.getBoundingClientRect();
      const mouseX = event.clientX - rect.left;

      const value = this.getXPositionToValue(mouseX);
      const dx = value - this.getXPositionToValue(this.startDragX);

      if (
        this.endValue + dx > this.maxValue ||
        this.startValue + dx < this.minValue
      ) {
        return;
      }
      this.startValue += dx;
      this.endValue += dx;
      this.throttledSliderMove(this.startValue);

      this.drawCanvas();

      this.startDragX = this.getValueToXPosition(value);
    },
    handleMouseUp() {
      if (!this.isDragging) {
        return;
      }
      this.isDragging = false;
      this.canvasCursor = 'pointer';
    },

    moveScale(dx) {
      this.$emit('move', dx);
    },
    updatePosition(newPosition) {
      const dx = newPosition - this.startValue;
      this.startValue = newPosition;
      this.endValue += dx;
      this.drawCanvas();
    },
  },
  watch: {
    canvasWidth() {
      this.drawCanvas();
    },
  },
};
</script>

<style scoped>
.range-slider {
  position: relative;
  height: 44px;
  background: #ffffff12;
  /* From https://css.glass */
  background: rgba(255, 255, 255, 0.15);
  border-radius: 6px;
  padding-left: 5px;
  padding-right: 5px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
}

canvas {
  cursor: pointer;
}
</style>
