<template>
  <div style="height: 284px">
    <div
      class="strip"
      :style="{ width: maxWidth - 250 + 'px' }"
      ref="image-container"
      @mousewheel.passive="scrollX"
    >
      <div class="video-trimmer-container" ref="video-trimmer-container">
        <VideoTrimmer
          :slice-obj="sliceObj"
          :BARHEIGHT="imageHeight"
          :BARWIDTH="videTrimWidth"
          :numImages="this.numImages"
          :imgWidth="imageWidth"
          :isHidden="!isHidden"
          ref="videotrimmer"
          @update-slice="handleUpdateSlice"
          @remove-label="handleRemoveLabel"
          @add-label="handleAddLabel"
        />
      </div>

      <div :style="{ height: heightHere + 'px' }" ref="moviestripcont">
        <canvas ref="movieStripCanvas"></canvas>
      </div>
      <Transition>
        <div v-if="hideinfo">
          <ImageBarImageInfo
            :numImages="this.numImages"
            :imgWidth="imageWidth"
            :imgHeight="heightHere"
          />
        </div>
      </Transition>
    </div>
  </div>
  <ImageStripSlider
    :numImages="this.numImages"
    :imgWidth="imageWidth"
    :slice-obj="sliceObj"
    :canvasWidth="maxWidth - 250"
    @updateDone="drawMovieStrip"
    @move="moveX"
    ref="sliderBar"
  />
</template>

<script>
import ImageBarImageInfo from './ImageBarImageInfo.vue';
import ImageStripSlider from './ImageStripSlider.vue';
import VideoTrimmer from './VideoTrimmer.vue';

export default {
  components: {
    ImageStripSlider,
    VideoTrimmer,
    ImageBarImageInfo,
  },
  emits: ['newstate', 'update-slice', 'remove-label', 'add-label'],
  props: {
    currentState: { type: String, default: 'FREEBREATHING' },
    maxWidth: { type: Number, default: 1560 },
    isHidden: { type: Boolean, default: true },
    hideinfo: { type: Boolean, required: true, default: true },
    numImages: {
      type: Number,
      default: 100,
    },
    id: { type: String },
    sliceObj: { type: Object, required: true },
  },
  data() {
    return {
      imageWidth: 286,
      requestId: null,
      videTrimWidth: null,
      imageHeight: 284,
      imageRatio: 0,
      originalImageWidth: 0,
      imageHeightWidthRatio: 0,
      heightHere: 284,
      viewState: 'FREEBREATHING',
    };
  },
  created() {
    this.videTrimWidth = this.imageWidth * this.numImages;
  },
  mounted() {
    const imgTemp = new Image();
    imgTemp.src = './images/test.png';

    imgTemp.onload = () => {
      this.imageHeightWidthRatio = imgTemp.naturalHeight / imgTemp.naturalWidth;
      this.imageHeight = this.imageWidth * this.imageHeightWidthRatio;
      this.$refs.videotrimmer.barHeight = this.imageHeight;
    };

    imgTemp.onerror = () => {
      console.error('Failed to load the image.');
    };

    this.drawMovieStrip();

    setTimeout(() => {
      this.$emit('loaded');
    }, 1500);
  },
  methods: {
    drawMovieStrip() {
      // Access the canvas element
      const canvas = this.$refs.movieStripCanvas;
      const context = canvas.getContext('2d');
      const { numImages } = this;

      canvas.width = this.imageWidth * numImages;
      canvas.height = this.imageHeight;
      this.videTrimWidth = canvas.width;

      // Clear the visible canvas
      context.clearRect(0, 0, canvas.width, canvas.height);

      // Draw your images on the offscreen canvas
      const images = Array(numImages).fill('./images/test.png');
      const { imageWidth } = this;

      let x = 0;

      // Create a list of image loading promises
      const imagePromises = images.map((image) => {
        return new Promise((resolve, reject) => {
          const img = new Image();
          img.src = image; // Use the correct path to the image

          img.onload = () => {
            resolve(img); // Resolve the promise with the loaded image
          };

          img.onerror = (error) => {
            reject(error); // Reject the promise on image loading error
          };
        });
      });

      // Wait for all images to load before drawing them
      Promise.all(imagePromises)
        .then((loadedImages) => {
          loadedImages.forEach((img) => {
            // this.imageHeight =
            //   img.naturalHeight / (img.naturalWidth / imageWidth);
            // this.originalImageWidth = img.naturalWidth;

            context.drawImage(img, x, 0, imageWidth, this.imageHeight);

            x += imageWidth;
          });
        })
        .catch((error) => {
          console.error(error); // Handle image loading errors
        });
    },

    scrollX(e) {
      const container = 'image-container';
      const containerElement = this.$refs[container];

      // Check if the Ctrl key is being held
      if (e.ctrlKey) {
        containerElement.scrollLeft += e.deltaY * 2.5;
        // You can add any other actions you want to perform when Ctrl key is held during scrolling.
      } else {
        // Perform the scrolling action as before
        containerElement.scrollLeft += e.deltaY / 4;
      }

      // Update the sliderBar instance
      const slideInstance = this.$refs.sliderBar;
      slideInstance.updatePosition(
        (100 * containerElement.scrollLeft) / containerElement.scrollWidth
      );
      this.getScrollState();
    },

    moveX(dx) {
      const container = `image-container`;
      this.$refs[container].scrollLeft =
        this.$refs[container].scrollWidth * (dx / 100);
      this.getScrollState();
    },
    getScrollState() {
      const states = ['FREEBREATHING', 'HYPERVENTILATION', 'BREATHHOLD'];

      const videoTrimObject = this.sliceObj;
      const scrollCont = this.$refs[`image-container`].scrollLeft;

      if (
        videoTrimObject.states[this.viewState].start.XLocation < scrollCont &&
        scrollCont < videoTrimObject.states[this.viewState].end.XLocation
      ) {
        // Do nothing, already in the correct state
      } else {
        const newState = states.find(
          (state) => scrollCont < videoTrimObject.states[state].end.XLocation
        );

        if (newState) {
          this.viewState = newState;
          this.$emit('newstate', this.viewState);
        }
      }
    },
    scrollAnimation(containerElement, scrollAmount) {
      const targetElement = containerElement;
      const start = targetElement.scrollLeft;
      // const end = start + scrollAmount;
      const duration = 300; // Animation duration in milliseconds (adjust as needed)
      let startTime;

      function step(timestamp) {
        if (!startTime) startTime = timestamp;
        const progress = timestamp - startTime;
        const percentage = Math.min(progress / duration, 1);

        targetElement.scrollLeft = start + scrollAmount * percentage;

        if (percentage < 1) {
          requestAnimationFrame(step);
        }
      }

      requestAnimationFrame(step);
    },
    scrollToState(state) {
      const containerElement = this.$refs['image-container'];

      const newloc = this.sliceObj.states[state].start.XLocation;

      const ds = newloc - containerElement.scrollLeft;

      // Update the sliderBar instance
      this.scrollAnimation(containerElement, ds);

      const slideInstance = this.$refs.sliderBar;
      slideInstance.updatePosition(
        (100 * newloc) / containerElement.scrollWidth
      );
    },

    handleUpdateSlice(slice) {
      this.$emit('update-slice', slice);
      this.$refs.sliderBar.drawCanvas();
    },
    handleRemoveLabel(obj) {
      this.$emit('remove-label', obj);
    },
    handleAddLabel(obj) {
      this.$emit('add-label', obj);
    },
  },
};
</script>

<style scoped>
.strip {
  overflow: hidden;
  overflow-y: hidden;
  scrollbar-width: none; /* Hide the scrollbar in modern browsers */
  -ms-overflow-style: none;
  border-radius: 2px;
  display: grid;
  grid-template-columns: 1fr;
  transform: all 0.3s;
}

.strip div {
  grid-row-start: 1;
  grid-column-start: 1;
}

.video-trimmer-container {
  overflow: hidden;
  overflow-y: hidden;
  scrollbar-width: none; /* Hide the scrollbar in modern browsers */
  -ms-overflow-style: none;
}

.slidecontainer {
  width: 100%; /* Width of the outside container */
}

.slider {
  width: 100%;
}

.v-enter-active,
.v-leave-active {
  transition: opacity 0.5s ease;
}

.v-enter-from,
.v-leave-to {
  opacity: 0;
}
</style>
