<template>
  <input
    :value="propValue"
    @input="updateProp"
    @blur="stopEditing"
    ref="inputField"
    :class="{ edit: editable }"
  />
</template>

<script>
export default {
  props: {
    id: { type: String, required: true },
    propValue: {
      type: String,
      required: true,
      default: '00',
    },
    editable: { type: Boolean, default: true },
  },
  data() {
    return {
      editing: 0,
      editedValue: this.propValue,
    };
  },
  methods: {
    stopEditing() {
      this.$emit('updateValue', this.id, this.editedValue);
    },
    updateProp(event) {
      this.editedValue = event.target.value;
    },
  },
  watch: {
    editable() {
      this.$refs.inputField.readOnly = !this.editable;
    },
  },
  mounted() {
    if (!this.editable) {
      this.$refs.inputField.readOnly = true;
    }
  },
};
</script>

<style scoped>
h5 {
  color: #000917;
  font-weight: bold;
  font-size: 13px;
  height: 17px;
  padding-left: 2%;
  width: 50px;
}

input {
  color: #000917;
  font-weight: bold;
  font-size: 13px;
  padding-left: 2%;
  outline: none;
  width: 50px;
  height: 17px;
  overflow: hidden;
  border: 1px dashed #ffffff00;
  transition: all 0.3s ease;
}

input.edit {
  border: 1px dashed #229ed7;
}
</style>
