<template>
  <div
    @contextmenu.prevent="openContextMenu"
    @click="closeContextMenu"
    @mousewheel.passive="closeContextMenu"
    @keypress.prevent="handleKeyPress"
  >
    <slot></slot>
    <transition name="fade">
      <div
        v-if="showContextMenu"
        class="custom-context-menu"
        :style="{ top: `${contextMenuY}px`, left: `${contextMenuX}px` }"
      >
        <div
          class="option"
          @click="addLabel('systoles')"
          @keypress="h"
          :class="{ noClick: hasLabel }"
        >
          <i class="fa-solid fa-plus fa-sm" style="color: #ffffff"></i>
          <h5>Add Systole Label</h5>
        </div>

        <div
          class="option"
          @click="addLabel('diastoles')"
          @keypress="h"
          :class="{ noClick: hasLabel }"
        >
          <i class="fa-solid fa-circle-plus fa-sm" style="color: #ffffff"></i>
          <h5>Add Diastole Label</h5>
        </div>

        <div
          class="option"
          @click="removeLabel()"
          @keypress="h"
          :class="{ noClick: !hasLabel }"
        >
          <i class="fa-regular fa-trash-can fa-sm" style="color: #ffffff"></i>
          <h5>Delete Label</h5>
        </div>
      </div>
    </transition>
    <transition name="fade">
      <div
        v-if="showError"
        class="custom-context-menu"
        :style="{ top: `${contextMenuY}px`, left: `${contextMenuX}px` }"
      >
        <div class="warning">
          <i
            class="fa-solid fa-triangle-exclamation"
            style="color: #ffcc00"
          ></i>
          <h5>Unable to edit label! Please click within a breathing state.</h5>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  props: {
    hasLabel: null,
  },
  data() {
    return {
      showContextMenu: false,
      showError: false,
      contextMenuX: 0,
      contextMenuY: 0,
    };
  },
  methods: {
    openContextMenu(event) {
      this.showContextMenu = true;
      this.contextMenuX = event.clientX;
      this.contextMenuY = event.clientY;
    },
    closeContextMenu() {
      this.showContextMenu = false;
      this.showError = false;
    },
    addLabel(option) {
      this.$emit('add-label', option);
      this.showContextMenu = false;
    },
    removeLabel() {
      this.$emit('remove-label');
      this.showContextMenu = false;
    },
    popError(event) {
      this.showError = true;
      this.contextMenuX = event.clientX;
      this.contextMenuY = event.clientY;
    },
    handleKeyPress() {},
  },
};
</script>

<style scoped>
.custom-context-menu {
  position: absolute;
  z-index: 999999;
  background-color: rgba(0, 0, 0, 0.74);
  box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  gap: 0.2rem;
  padding: 0.5rem 0rem 0.5rem 0rem;
  width: 200px;
}

.option {
  width: 100%;

  padding: 0.2rem 0.5rem 0.2rem 0.5rem;
  display: flex;
  gap: 0.5rem;
  align-items: center;
}
.option:hover {
  background: #79cfde98;
  transition: all 0.3s ease;
}

.warning {
  color: #ffcc00;
  display: flex;
  gap: 0.4rem;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-left: 0.5rem;
  text-align: center;
}

.option.noClick {
  opacity: 0.3;
  pointer-events: none;
}
/* Styles for the fade-in animation */
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.3s ease;
}

.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}
</style>
